import React, { useState } from 'react';
import styles from './Form.module.css';
import citydata from "./city.json"

const Form = ({ onSubmit }) => {
  const [formData, setFormData] = useState({
    name: '',
    gender: '男', // 设置默认值
    calendarType: '公历', // 设置默认值
    year: '',
    month: '',
    day: '',
    hour: '00',
    minute: '00',
    province: '',
    city: ''
  });

  // 生成年份选项（1900-2050）
  const years = Array.from({length: 151}, (_, i) => 1900 + i);
  // const months = Array.from({length: 12}, (_, i) => i + 1);
  const days = Array.from({length: 31}, (_, i) => i + 1);
  // 农历月份选项，包括可能的闰月
  const updateMonthOptions = () => {
    if (formData.calendarType === '农历') {
      return [
        '正月', '二月', '三月', '四月', '五月', '六月',
        '七月', '八月', '九月', '十月', '十一月', '十二月',
        '闰正月', '闰二月', '闰三月', '闰四月', '闰五月', '闰六月',
        '闰七月', '闰八月', '闰九月', '闰十月', '闰十一月', '闰十二月'
      ];
    }
    return Array.from({length: 12}, (_, i) => i + 1);
  };
  
  // 生成时间选项
  const hours = Array.from({length: 24}, (_, i) => i.toString().padStart(2, '0'));
  const minutes = Array.from({length: 60}, (_, i) => i.toString().padStart(2, '0'));
  //设置省份、城市规则
  const provinces = citydata.city.filter(item => item.pid === 0);
  const cities = citydata.city.filter(item => item.pid !== 0);

  const handleProvinceChange = (e) => {
    setFormData({
      ...formData,
      province: e.target.value,
      city: 'unknown'
    });
  };

  const handleCityChange = (e) => {
    setFormData({ ...formData, city: e.target.value });
  };

  const filteredCities = cities.filter(city => {
    const selectedProvince = provinces.find(prov => prov.name === formData.province);
    return selectedProvince && city.pid === selectedProvince.id;
  });
  

// 确保在组件顶部定义了handleSubmit函数
const handleSubmit = (e) => {
  e.preventDefault(); // 必须阻止默认行为
  // console.log('提交数据：', formData);
  
  // 添加基础验证
  if (!formData.year || !formData.month || !formData.day) {
    alert('请选择完整的出生日期');
    return;
  }
  if (formData.province === 'unknown' || formData.city === 'unknown') {
    alert('请选择完整的地区信息');
    return;
  }

  //性别判断
  let genders
  if (formData.gender === '男') { 
    genders = 0;
  }else {
      genders = 1;
    }
  //农历转公历
  // 月份映射表
  const monthMap = {
    '正月': 1, '二月': 2, '三月': 3, '四月': 4, '五月': 5, '六月': 6,
    '七月': 7, '八月': 8, '九月': 9, '十月': 10, '十一月': 11, '十二月': 12,
    '闰正月': 1, '闰二月': 2, '闰三月': 3, '闰四月': 4, '闰五月': 5, '闰六月': 6,
    '闰七月': 7, '闰八月': 8, '闰九月': 9, '闰十月': 10, '闰十一月': 11, '闰十二月': 12
  };

  let solarDate;
  let lunarMonth = monthMap[formData.month]; 
  let isLeap = false;
  if (formData.calendarType === '农历') {
    // console.log('确定农历日期：', formData.year, lunarMonth, formData.day);
    if (formData.month.startsWith('闰')) {
      isLeap = true;
    }
    const lunarDate = window.p.Lunar2Solar(formData.year, lunarMonth, formData.day, isLeap);
    // console.log('转换后的公历日期：', lunarDate);
    if (lunarDate === false) {
      alert('请检查农历日期是否正确');
      return;
    }
    solarDate = { year: lunarDate[0], month: lunarDate[1], day: lunarDate[2] };
    
    // console.log('转换后的公历日期：', solarDate);
  } else {
    solarDate =  { year: formData.year, month: formData.month, day: formData.day }; // 公历下直接使用原月份值
  }
  //经纬度
  const selectedCity = cities.find(c => c.name === formData.city);
  if (!selectedCity) {
    alert('请选择有效的城市信息');
    return;
  }
  const J = selectedCity.j;
  const W = selectedCity.w;
  //八字排盘
  const eightCharacters = window.p.fatemaps(
    genders,
    solarDate.year,
    solarDate.month,
    solarDate.day,
    parseInt(formData.hour),
    parseInt(formData.minute),
    0,
    J,
    W
  );
 
  const transformUserData = (raw, datetimeStr) => {
    let txt = "";
    // 生成八字信息
      // 添加出生时间信息
    txt += "出生时间：" + datetimeStr + "\n";
    txt += "八字：";
    txt += raw.sz.join(', ') + "\n";
    // 生成性别信息
    txt += "性别：" + raw.xb + "\n";
    // 生成大运信息
    txt += "大运：";
    let dyItems = [];
    // 限制遍历5次
    for (let i = 0; i < 7 && raw['dy'] && i < Object.keys(raw['dy']).length; i++) {
      let a = raw['dy'][i];
      dyItems.push(`${a['zfma']}${a['zfmb']}`);
    }
    txt += dyItems.length > 0 ? dyItems.join(', ') + "\n" : "无大运数据\n";
    return txt; // 确保函数返回生成的文本
  };
  
  // console.log(eightCharacter);
  const datetimeStr = [
    formData.name,
    solarDate.year,
    solarDate.month,
    solarDate.day,
    `${parseInt(formData.hour)}:${parseInt(formData.minute).toString().padStart(2, '0')}`
  ].join('/');
  const eightCharacter = transformUserData(eightCharacters,datetimeStr);
  // console.log(eightCharacter);
  onSubmit( eightCharacter); // 确保父组件传递了onSubmit
};


  return (
    <form onSubmit={handleSubmit} className={styles.formContainer}>
      {/* 姓名行 */}
      <div className={styles.formRow}>
        <div className={styles.inputGroup}>
          <input
            type="text"
            placeholder="请输入您的姓名（选填）"
            className={styles.underlineInput}
            value={formData.name}
            onChange={(e) => setFormData({...formData, name: e.target.value})}
          />
        </div>
      </div>

      {/* 性别和历法选择行 */}
      <div className={styles.formRow}>
        <div className={styles.buttonGroup}>
          {['男', '女'].map((gender) => (
            <button
              key={gender}
              type="button"
              className={`${styles.choiceButton} ${
                formData.gender === gender ? styles.active : ''
              }`}
              onClick={() => setFormData({...formData, gender})}
            >
              {gender}
            </button>
          ))}
        </div>
        <div className={styles.buttonGroup}>
          {['公历', '农历'].map((type) => (
            <button
              key={type}
              type="button"
              className={`${styles.choiceButton} ${
                formData.calendarType === type ? styles.active : ''
              }`}
              onClick={() => setFormData({...formData, calendarType: type})}
            >
              {type}
            </button>
          ))}
        </div>
      </div>

      {/* 出生日期选择行 */}
      <div className={styles.formRow1}>
        <div className={styles.dateSelectGroup}>
        <select 
            value={formData.year}
            onChange={(e) => setFormData({...formData, year: e.target.value})}
            className={styles.dateSelect}
          >
            <option value="">年</option>
            {years.map((year) => (
              <option key={year} value={year}>{year}</option>
            ))}
          </select>
          <select
            value={formData.month}
            onChange={(e) => setFormData({...formData, month: e.target.value})}
            className={styles.dateSelect}
          >
            <option value="">月</option>
            {updateMonthOptions().map((month, index) => (
              <option key={index} value={month}>{month}</option>
            ))}
          </select>
          <select
            value={formData.day}
            onChange={(e) => setFormData({...formData, day: e.target.value})}
            className={styles.dateSelect}
          >
            <option value="">日</option>
            {days.map((day) => (
              <option key={day} value={day}>{day}</option>
            ))}
          </select>
        </div>
      </div>

      {/* 出生时间选择 */}
      <div className={styles.formRow}>
        <div className={styles.timeSelectGroup}>
          <select
            value={formData.hour}
            onChange={(e) => setFormData({...formData, hour: e.target.value})}
            className={styles.dateSelect}
          >
            {hours.map((hour) => (
              <option key={hour} value={hour}>{hour}时</option>
            ))}
          </select>
  
          <select
            value={formData.minute}
            onChange={(e) => setFormData({...formData, minute: e.target.value})}
            className={styles.dateSelect}
          >
            {minutes.map((minute) => (
              <option key={minute} value={minute}>{minute}分</option>
            ))}
          </select>
        </div>
      </div>

      {/* 省市联动选择行 */}
  
      <div className={styles.formRow3}>
        <select
          value={formData.province}
          onChange={handleProvinceChange}
          className={styles.underlineInput}
          style={{ marginRight: '20px' }}
        >
          <option value="unknown">选择省份</option>
          {provinces.map(province => (
            <option key={province.id} value={province.name}>{province.name}</option>
          ))}
        </select>

        <select
          value={formData.city}
          onChange={handleCityChange}
          className={styles.underlineInput}
          disabled={!formData.province || formData.province === 'unknown'}
        >
          <option value="unknown">
            {formData.province === 'unknown' ? '先选择省份' : '选择城市'}
          </option>
          {filteredCities.map(city => (
            <option key={city.id} value={city.name}>{city.name}</option>
          ))}
        </select>
      </div>
          

      {/* 排盘按钮行 */}
      <div className={styles.formRow}>
        <button type="submit" className={styles.submitButton}>
          立即解锁命盘玄机
        </button>
      </div>
    </form>
  );
};

export default Form;