import React, { useState, useEffect, useMemo } from 'react';
import Form from './components/Form';
import PaymentModal from './components/PaymentModal';
import AnalysisSection from './components/AnalysisSection';
import './App.css';
import { createOrderStream, initiatePayment, fetchAnalysisContent } from './api';

const ANALYSIS_TYPES = [
  { id: 'health', name: '健康分析' },
  { id: 'career', name: '事业分析' },
  { id: 'wealth', name: '财运分析' },
  { id: 'marriage', name: '婚姻分析' }
];

function App() {
  const [analysisData, setAnalysisData] = useState({
    currentOrderId: null,
    preview: '',
    unlocked: {},
    contents: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    },
    analysisOrderIds: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    }
  });
  const [paymentState, setPaymentState] = useState({
    show: false,
    qrcodeUrl: '',
    processingType: null
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [es, setES] = useState(null);

  // 本地存储操作封装，使用useMemo避免重复创建
  const storage = useMemo(() => ({
    save: (data) => {
      if (typeof data !== 'object' || data === null) {
        setError('无法保存无效的分析数据');
        return false;
      }
  
      try {
        localStorage.setItem('analysisData', JSON.stringify(data));
        return true;
      } catch (error) {
        setError('保存数据失败，请检查浏览器存储空间');
        return false;
      }
    },
  
    load: () => {
      try {
        const data = localStorage.getItem('analysisData');
        if (!data) return null;
  
        const parsedData = JSON.parse(data);
  
        // 数据格式校验
        if (
          typeof parsedData === 'object' &&
          parsedData !== null &&
          typeof parsedData.preview === 'string' &&
          typeof parsedData.unlocked === 'object' &&
          typeof parsedData.contents === 'object' &&
          typeof parsedData.currentOrderId === 'string' &&
          typeof parsedData.analysisOrderIds === 'object'
        ) {
          return parsedData;
        }
        
        throw new Error('数据格式不正确');
      } catch (error) {
        localStorage.removeItem('analysisData');
        return null;
      }
    }
  }), [setError]);

  const initialAnalysisState = {
    currentOrderId: null,
    preview: '',
    unlocked: {},
    contents: { health: '', career: '', wealth: '', marriage: '' },
    analysisOrderIds: { health: null, career: null, wealth: null, marriage: null }
  };

  const clearAnalysisData = () => {
    try {
      localStorage.removeItem('analysisData');
      setAnalysisData(initialAnalysisState);
    } catch (error) {
      setError('清除数据失败');
    }
  };

  // 刷新分析内容
  const refreshAnalysis = async (type) => {
    try {
      if (!analysisData.currentOrderId || !analysisData.analysisOrderIds[type]) {
        throw new Error('无效的订单信息，请重新生成分析');
      }

      setLoading(true);
      const content = await fetchAnalysisContent(
        analysisData.currentOrderId,
        type,
        analysisData.analysisOrderIds[type]
      );

      setAnalysisData(prev => ({
        ...prev,
        contents: {
          ...prev.contents,
          [type]: content
        }
      }));
    } catch (error) {
      setError(error.message || '刷新内容失败');
    } finally {
      setLoading(false);
    }
  };
  
  const handleSubmit = async (eightCharacters) => {
    setLoading(true);
    setError('');
    setAnalysisData(initialAnalysisState);

    const newES = createOrderStream(eightCharacters, {
      onStream: (chunk) => {
        setAnalysisData(prev => ({
          ...prev,
          preview: prev.preview + chunk
        }));
        setLoading(false);
      },
      onComplete: (prevOrderId) => {
        setAnalysisData(prev => ({
          ...prev,
          currentOrderId: prevOrderId
        }));
      },
      onError: (message) => {
        setError(message || '生成预览失败');
        setLoading(false);
      }
    });
    setES(newES);
  };

  const unlockAnalysis = (type) => {
    if (!analysisData.currentOrderId) return;

    const initialPaymentState = {
      show: true,
      qrcodeUrl: '',
      processingType: type
    };
    setPaymentState(initialPaymentState);

    const paymentES = initiatePayment(analysisData.currentOrderId, type, {
      onPaymentQR: (qrcodeUrl, orderId) => {
        setPaymentState(prev => ({ ...prev, qrcodeUrl }));
        setAnalysisData(prev => ({
          ...prev,
          analysisOrderIds: {
            ...prev.analysisOrderIds,
            [type]: orderId
          }
        }));
      },
      onAnalysisChunk: (chunk) => {
        setAnalysisData(prev => ({
          ...prev,
          contents: {
            ...prev.contents,
            [type]: (prev.contents[type] || '') + chunk
          }
        }));
      },
      onPaymentstatus: () => {
        setAnalysisData(prev => ({
          ...prev,
          unlocked: { ...prev.unlocked, [type]: true }
        }));
        setPaymentState({ show: false, qrcodeUrl: '', processingType: null });
      },
      onAnalysisEnd: () => {
        // 可以添加分析完成的处理逻辑
      },
      onError: (message) => {
        setError(message || '支付流程出错');
        setPaymentState({ show: false, qrcodeUrl: '', processingType: null });
      }
    });

    setES(paymentES);
  };

  // 保存分析数据到本地存储
  useEffect(() => {
    if (analysisData.preview) {
      storage.save(analysisData);
    }
  }, [analysisData, storage]);

  // 初始加载和清理
  useEffect(() => {
    const loadedData = storage.load();
    if (loadedData) {
      setAnalysisData(loadedData);
    }

    return () => {
      if (es) {
        es.close();
      }
    };
  }, [es, storage]);


  return (
    <div className="App">
      <header className="header">
        <div className="header-content">
          <div className="branding">
            <h1>MingLi理命</h1>
            <p className="slogan">一阴一阳之谓道</p>
          </div>
          <div className="logo-container">
            <img src="/logo.png" alt="Logo" className="logo" />
          </div>
        </div>
      </header>

        {error && <div className="error-message">{error}</div>}
        
        {loading ? (
            <div className="loading-wrapper">
            <div className="loading">
              ⏳ 深度分析通常需要 40~60 秒
              <div className="sub-hint">（期间请不要刷新页面）</div>
            </div>
          </div>
        ) : (
          !analysisData.preview ? (
            <Form onSubmit={handleSubmit} />
          ) : (
            <AnalysisSection
              preview={analysisData.preview}
              unlocked={analysisData.unlocked}
              contents={analysisData.contents}
              onUnlock={unlockAnalysis}
              onRefresh={refreshAnalysis}
              analysisTypes={ANALYSIS_TYPES}
              onGoBack={clearAnalysisData}
              onComplete={analysisData.currentOrderId}
            />
          )
        )}

        <PaymentModal
          isOpen={paymentState.show}
          qrcodeUrl={paymentState?.qrcodeUrl}
          onClose={() => setPaymentState({ show: false, qrcode: '', processingType: null })}
        />
    
      <footer>
        <div className="sfooterContent">
          <p className="sdisclaimer">
            拒绝向未成年人提供服务，仅供娱乐和参考。
          </p>
          <p className="sdisclaimer">
          ©2025 MingLi理命
          </p>
        </div>
      </footer>

    </div>
    
  );
}

export default App;



